<template>
	<view class="content">
		<view class="mask"></view>
		<view class="top-bar">
			<view class="top-bar-l">
				<view class="iconfont" @tap="back">&#xe779;</view>
			</view>
			<view class="top-bar-center">详情</view>
		</view>
		<view class="main">
			<view class="headerCol">
				<view class="head">
					<view class="title">头像</view>
					<view class="content">
						<image class="user-img" :src="userImg" @tap="isContainer"></image>
					</view>
					<view class="more">&#xe775</view>
				</view>
				<view class="head">
					<view class="title">签名</view>
					<view class="content">
						萨达萨达萨达萨达萨达萨达萨达萨达萨达萨达萨达萨达萨达萨达萨达
					</view>
					<view class="more">&#xe775</view>
				</view>
				<view class="head">
					<view class="title">注册</view>
					<view class="content">
						2020-4-20
					</view>
					<!-- <view class="more">&#xe775</view> -->
				</view>
				<view class="head">
					<view class="title">昵称</view>
					<view class="content">
						蓝色妖姬
					</view>
					<view class="more">&#xe775</view>
				</view>
				<view class="head">
					<view class="title">性别</view>
					<view class="content">
						<!-- 这里使用了uniapp官方提供的选择器组件进行性别选择 -->
						<picker @change="bindPickerChange" :value="index" :range="array">
							<view class="uni-input">{{array[index]}}</view>
						</picker>
					</view>
					<view class="more">&#xe775</view>
				</view>
				<view class="head">
					<view class="title">电话</view>
					<view class="content">
						4864864846
					</view>
					<view class="more">&#xe775</view>
				</view>
				<view class="head">
					<view class="title">邮箱</view>
					<view class="content">
						845486@qq.com
					</view>
					<view class="more">&#xe775</view>
				</view>
				<view class="head">
					<view class="title">密码</view>
					<view class="content">
						*********
					</view>
					<view class="more">&#xe775</view>
				</view>
			</view>
			<view class="bt">退出登录</view>
		</view>
		<!-- 裁剪工具 -->
		<view class="container" v-if="containerIs">
			<view class="cropper-wrap">
				<image-cropper id="image-cropper" :zoom="1" :angle="0" :src="src" canvasBackground="red"
					@cropped="cropped" @afterDraw="afterDraw" @beforeDraw="beforeDraw" />

			</view>
			<button @tap="selectImg">上传图片</button>
		</view>
		<!-- 裁剪工具 -->
	</view>
</template>

<script>
	import ImageCropper from '../../components/uniapp-nice-cropper/cropper.vue'
	export default {
		data() {
			return {
				userImg: '../../static/images/image/3.jpeg',
				index: '0',
				array: ['男', '女'],
				src: '',
				containerIs: false
			}
		},
		methods: {
			back() {
				uni.navigateBack({
					delta: 1
				})
			},
			bindPickerChange(e) {
				this.index = e.detail.value
			},
			//裁剪插件方法--------------------
			selectImg() {
				uni.chooseImage({
					count: 1,
					sizeType: ['original'],
					sourceType: ['album', 'camera'],
					success: res => {
						var tempFilePaths = res.tempFilePaths
						this.src = tempFilePaths[0]
					}
				})
			},
			beforeDraw(context, transform) {
				context.setFillStyle('yellow')
				transform.zoom = 2
			},
			afterDraw(ctx, info) {
				ctx.fillText('我是一行文字水印', 20, 20)
				console.log(`当前画布大小：${info.width}*${info.height}`)

			},
			cropped(imagePath, imageInfo) {
				console.log(imagePath, imageInfo)
				this.userImg = imagePath

			},
			//裁剪插件方法-----------------
			isContainer() {
				this.containerIs = !this.containerIs
			}
		},
		components: {
			ImageCropper
		},
	}
</script>

<style lang="scss">
	@font-face {
		font-family: 'iconfont';
		src: url('../../static/images/common/font/iconfont.woff2?t=1656943243731') format('woff2'),
			url('../../static/images/common/font/iconfont.woff?t=1656943243731') format('woff'),
			url('../../static/images/common/font/iconfont.ttf?t=1656943243731') format('truetype');
	}

	.content {
		padding-top: var(--status-bar-height);
	}

	.top-bar {
		position: fixed;
		z-index: 1000;
		top: var(--status-bar-height);
		left: 0;
		width: 100%;
		height: 88rpx;
		background: rgba(255, 255, 255, 0);
		border-bottom: 1px solid rgba(39, 40, 50, 0.1);

		.top-bar-l {
			float: left;
			padding-left: 32rpx;
			line-height: 88rpx;
			width: 88rpx;

			.iconfont {
				font-family: "iconfont" !important;
				font-size: 48rpx;
				font-style: normal;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}
		}

		.top-bar-center {
			font-size: 40rpx;
			color: #272832;
			font-weight: 400;
			line-height: 88rpx;
			text-align: center;
			margin-right: 78rpx;
		}
	}

	.main {
		padding-top: 118rpx;
		display: flex;
		flex-direction: column;

		.headerCol {
			display: flex;
			flex-direction: column;

			.head {
				display: flex;
				flex-direction: row;


				.title {
					flex: none;
					margin: auto;
					padding: 0 32rpx;
					font-size: 32rpx;
					color: #272832;
					font-weight: 400;
					line-height: 112rpx;

				}

				.content {
					flex: auto;
					font-size: 32rpx;
					color: rgba(39, 40, 50, 0.6);
					line-height: 112rpx;
					/*1．先强制一行内显示文本*/
					white-space: nowrap; //(默认normal自动换行)
					/*2．超出的部分隐藏*/
					overflow: hidden;
					/*3．文字用省略号替代超出的部分*/
					text-overflow: ellipsis;

					image {
						width: 108rpx;
						height: 100%;
						border-radius: 20rpx;
					}
				}

				.more {
					flex: none;
					margin: auto;
					font-family: "iconfont" !important;
					font-size: 38rpx;
					font-style: normal;
					-webkit-font-smoothing: antialiased;
					-moz-osx-font-smoothing: grayscale;

				}
			}
		}

		.bt {
			padding-top: 160rpx;
			text-align: center;
			line-height: 44rpx;
			font-size: 36rpx;
			color: #FF5D5B;
			font-weight: 500;
		}
	}

	.container {
		position: fixed;
		top: 260rpx;
		height: 400rpx;
		width: 100%;
		.cropper-wrap {
			height: calc(100% - 96rpx);
		}
	}

	
</style>
